
.login-layer {
  width: 100%;
  height: 4rem;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}
.login-mainheader-bar {
  width: 100%;
  height: 0.4rem;
  text-align: center;
  border-bottom: 1px solid #EFEFF0;
  background-color: #ffffff;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 222;
}
.login-mainheader-bar .login-back-btn {
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat;
  margin: 0.1rem 0.1rem;
}
.login-mainheader-bar .login-title {
  display: inline-block;
  font-size: 16px;
  line-height: 0.4rem;
  font-weight: 600;
  color: #040404;
}
.login-main {
  width: 100%;
  height: 4rem;
  background-color: #ffffff;
  padding-top: 0.4rem;
}
.login-main > .login-se-logo {
  width: 100%;
  height: 0.7rem;
  margin-top: 0.34rem;
  text-align: center;
}
.login-box {
  width: 100%;
  height: 100%;
  margin-top: 0.34rem;
  padding: 0 0.48rem;
}
.login-box .user-name-box {
  width: 100%;
  height: 0.4rem;
  margin-top: 10px;
}
.login-box .user-name-box input {
  border: 1px solid #d6d6d6;
  border-radius: 0.04rem;
  width: 100%;
  height: 0.38rem;
  padding: 0.09rem 0.3rem;
  font-size: 16px;
  -webkit-user-modify: read-write-plaintext-only;
}
.login-box > .user-login-btn {
  margin-top: 0.2rem;
}
.login-box > .user-login-btn .user-login-btnc {
  width: 100%;
  height: 0.38rem;
  line-height: 0.38rem;
  display: block;
  background: #20a0ff;
  text-align: center;
  color: #ffffff;
  font-size: 18px;
}
.user-name-box2 {
  position: relative;
}
.user-name-box2 .user-icon {
  position: absolute;
  left: 0.12rem;
  top: 0.10rem;
  width: 0.14rem;
  height: 0.16rem;
  background: url("../images/user_icon@2x.png") no-repeat;
  background-size: 0.14rem 0.16rem;
}
.user-pawd-box2{
  position: relative;
}
.user-name-box2 .user-close-icon {
  position: absolute;
  right: 0.12rem;
  top: 0.1rem;
  width: 0.18rem;
  height: 0.18rem;
  background: url("../images/close-icon.png") no-repeat;
  display: none;

}
.user-pawd-box2 #pwd-close{
  position: absolute;
  right: 0.12rem;
  top: 0.1rem;
  width: 0.18rem;
  height: 0.18rem;
  background: url("../images/close-icon.png") no-repeat;
  display: none;
}
.login-box .user-pawd-box2 {
  position: relative;
  margin-top: 0.2rem;
}
.user-pawd-box2 .u-passwd-iconbox {
  position: absolute;
  left: 0.12rem;
  top: 0.1rem;
  width: 14px;
  height: 16px;
}
.u-passwd-iconbox > .pwd-icon-top {
  position: absolute;
  width: 9px;
  height: 11px;
  left: 0.02rem;
  background: url("../images/suo-icon.png") no-repeat;
}
.u-passwd-iconbox > .pwd-icon-bottom {
  position: absolute;
  width: 13px;
  height: 8px;
  top: 0.11rem;
  background: url("../images/suo-icon-bottom.png") no-repeat;
}
.forget-paswd > .reg-quick-btn {
  font-size: 14px;
  color: #20a0ff;
}
.forget-paswd > .forget-paswd-btn {
  float: right;
  font-size: 14px;
  color: #959595;
}
.login-se-img{
  width: 2.13rem;
  height: 0.7rem;
}



