.reg-layer {
  width: 100%;
  height: 100%;
  background: #ffffff;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

.reg-title-box {
  width: 100%;
  height: 0.4rem;
  text-align: center;
  border-bottom: 1px solid #EFEFF0;
  background-color: #ffffff;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 222;
}

.reg-title-box > .reg-back {
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat;
  margin: 0.1rem 0.1rem;
}

.reg-title-box .reg-title {
  display: inline-block;
  font-size: 16px;
  line-height: 0.4rem;
  font-weight: 600;
  color: #040404;
}

.reg-info-box {
  width: 100%;
  padding: 0.4rem 0 0 0.1rem;
  height: 100%;
}

.reg-info-box div {
  width: 100%;
  height: 0.5rem;
  text-align: left;
  margin-top: 0.12rem;
  border-bottom: 1px solid #F9F9F9;
}

.reg-info-box div input {
  width: 80%;
  height: 0.38rem;
  padding: 0.16rem 0.17rem;
  font-size: 16px;
}

.reg-code {
  position: relative;
}

.reg-code .g-code {
  width: 1rem;
  position: absolute;
  right: 0.32rem;
  bottom: 0.2rem;
  border: 1px solid #20a0ff;
  border-radius: 0.04rem;
  color: #20a0ff;
  background: #ffffff;
  z-index: 444;
}

.reg-info-box > .reg-btn {
  margin-top: 0.2rem;
  padding-right: 0.1rem;
}

.reg-info-box > .reg-btn .reg-btn-info {
  width: 100%;
  height: 0.38rem;
  border: 1px solid #d6d6d6;
  border-radius: 0.04rem;
  line-height: 0.38rem;
  display: block;
  background: #20a0ff;
  text-align: center;
  color: #ffffff;
  font-size: 18px;

}
.reg-info-box .who{
  width: 100%;
  height: 0.51rem;
  text-align: left;
}
.who .my-namee{
  height: 0.51rem;
  width: 15%;
  display: inline-block;
  text-align: left;
  float: left;
}
.reg-info-box .who-am {
  width: 80%;
  display: inline-block;
  text-align: left;
  margin-top: 0;
}

.who-am > .who-box {
  text-align: left;
}

.who-am > .who-box, .who-am > .who-box > .radio-img {
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  margin-top: 0;
  margin-left: 20px;
}
.reg-info-box .fileee{
  width: 100%;
  height: 1rem;
}
.fileee .upload-file {
  float: left;
  position: relative;
  width: 1rem;
  height: 1rem;
  margin-top: 0;
  left: 0.2rem;
}
.fileee .card_n{
  font-size: 16px;
  display: inline-block;
  height: 1rem;
  width: 15%;
  margin-top: 0;
  line-height: 1rem;
  float: left;
}
.upload-file .select-file {
  position: absolute;
  width: 1rem;
  height: 1rem;
  margin: 0;
  border: 1px solid #D9D9D9;

}

.upload-file .select-file:before, .upload-file .select-file:after {
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: #D9D9D9
}

.upload-file .select-file:before {
  width: 0.02rem;
  height: 0.6rem
}

.upload-file .select-file:after {
  width: 0.6rem;
  height:0.02rem
}

.upload-file .select-file input[type=file] {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  filter: alpha(opcity=0);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0)
}

.upload-file .preview-img {
  position: absolute;
  width: 1rem;
  height: 1rem
}

.upload-file .close {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 20;
  width: 0.2rem;
  height: 0.2rem;
  line-height: 0.16rem;
  background: red;
  text-align: center;
  font-size: 20px;
  opacity: .6;
  color: #fff
}

.upload-file .close:hover {
  opacity: 1
}

.reg-info-box .agree {
  width: 100%;
  height: 0.4rem;
  display: flex;
}

.agree .tick {
  width: 0.26rem;
  height: 0.26rem;
  float: left;
  margin: 7px;
  border-radius: 0.26rem
}

.reg-info-box .agree .bg-red {
  background: url(../images/check_box.png) no-repeat center;
  width: 0.3rem;
  height: 0.4rem;
  background-size: 0.3rem;
  margin-top: 0;
  line-height: 0.4rem;
  margin-right: 0.1rem;
}
.reg-info-box .agree .bg-default {
  background: url(../images/check_box_default.png) no-repeat center;
  width: 0.3rem;
  height: 0.4rem;
  background-size: 0.3rem;
  margin-top: 0;
  line-height: 0.4rem;
  margin-right: 0.1rem;
}

.reg-info-box .agree .bg-eee {
  background-color: #eee
}


.reg-info-box .agree .tick img {
  display: block;
  width: 0.15rem;
  height: 0.12rem;
  margin: 0.08rem 0 0 0.06rem
}

.reg-info-box .agree .agreement {
  height: 0.4rem;
  line-height: 0.4rem;
  float: left;
  font-size: 14px;
  color: #626161;
  margin-top: 0;
}

.reg-info-box .agree .agreement a {
  color: #1877EB
}

.account_company {
  background: url(../images/g01.png) no-repeat center;
  border-radius: 50%;
}

.account_company_no {
  background: url(../images/g00.png) no-repeat center;
  border-radius: 50%;
}

.account_person {
  background: url(../images/xu00.png) no-repeat center;
  border-radius: 50%;
}

.account_person_have {
  background: url(../images/xu01.png) no-repeat center;
  border-radius: 50%;
}

.account_tec_have {
  background: url(../images/ji01.png) no-repeat center;
  border-radius: 50%;
  background-size: 50px;
}

.account_tec {
  background: url(../images/ji02.png) no-repeat center;
  border-radius: 50%;
  background-size: 50px;
}

.reg-password .company-list {
  position: absolute;
  top: 0.4rem;
  left: 0;
  z-index: 9999;
  width: 100%;
  padding: 4px 0;
  margin: 0;
  border: 1px solid #eee;
  background: #fff;
  max-height: 1.8rem;
  overflow: scroll;
  list-style: none;
}

.reg-password .company-list li {
  font-size: 14px;
  padding: 0.06rem 0.1rem;
}
.choice_company{
  position: relative;
}



