
.database-content-box {
    height: 100%;
    max-width: 7.5rem;
    min-width: 3.2rem;
    background: #ffffff;
    margin: 0 auto;
}
.ul-databse{
  width: 100%;
  height: 100%;
  padding-top: 0.4rem;
}
.ul-databse-search{
  width: 100%;
  height: 100%;
}
.database-content-box>.my-line{
    width: 100%;
    height: 0.1rem;
    background: #EDEEF0;
}
.database-content-box .download-item {
    width: 100%;
    height: 1rem;
    padding-top: 0.1rem;
    border-bottom: 1px solid #ccc;
    overflow: hidden;
}
.download-item > .download-item-link {
    display: block;
    width: 100%;
    height: 1rem;
    padding: 0.1rem;

}
.download-item > .download-item-link:nth-child(1) {
    padding-top: 0;
}
.download-item-link > .download-item-pic {
    width: 0.8rem;
    height: 0.8rem;
    position: absolute;
}
.download-item-link > .download-r {
    width: 100%;
    height: 1rem;
    margin-left: 0.9rem;
    overflow: hidden;
}
.download-r > .download-home-title {
    color: #2d2d2d;
    text-align: left;
    font-size: 18px;
    font-weight: 600;
    height: 0.2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 0.2rem;
    width: 250px;
}
.download-r > .download-content {
    width: 100%;
    height: 37px;
    font-size: 14px;
    color: #3e3e3e;
    margin-top: 0.05rem;
    overflow: hidden;
    padding-bottom: 4px;
    word-break: break-all;
  /*padding-right: 0.12rem;*/
    padding-right: 0.8rem;
}
.download-r > .download-time-box {
    margin-top: 0.05rem;
    color: #9c9c9c;
    font-size: 12px;
    height: 0.14rem;
    overflow: hidden;
}



