.mine_info_layer{
  width: 100%;
  height: 100%;
}
.mine-header-bar {
  width: 100%;
  height: 0.4rem;
  text-align: center;
  background: #ffffff;
  overflow:scroll;
  top: 0;
  left: 0;
}
.mine-header-bar .login-back-btn {
  float: left;
  width: 0.4rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat;
  margin: 0.1rem 0.1rem;
}
.mine-header-bar .login-title {
  display: inline-block;
  font-size: 16px;
  line-height: 0.4rem;
  font-weight: 600;
  color: #2d2d2d;
}
.mine-header-bar .topbar-left {
  float: right;
  font-size: 16px;
  color: #010101;
  line-height: 0.4rem;
  margin-right: 0.14rem;
}
.mine-u-info {
  width: 100%;
  height: 100%;
  border-top: 1px solid #ededed;

}
.mine-u-info > .user-info-sec {
  width: 100%;
  height: 0.86rem;
  padding: 0.12rem 0.1rem;
  background: #ffffff;
}
.user-info-sec > .u-name-box {
  float: left;
  width: 1rem;
  height: 0.62rem;
  -webkit-position: relative;
  -moz-position: relative;
  -ms-position: relative;
  -o-position: relative;
  position: relative;
}
.u-name-box > .u-icon {
  top: 0.22rem;
  position: absolute;
  width: 0.18rem;
  height: 0.18rem;
  display: inline-block;
  background: url("../images/u-iicon@3x.png") no-repeat center;
  background-size: 0.18rem 0.18rem;
}
.u-name-box > .u-name {
  line-height: 0.62rem;
  margin-left: 0.28rem;
  color: #2d2d2d;
  font-weight: 500;
  font-size: 16px;
}
.u-photo-box {
  float: right;
  width: 1rem;
  height: 0.62rem;
  position: relative;
}
.u-photo-box .u-photo {
  display: inline-block;
  width: 0.62rem;
  height: 0.62rem;
  border-radius: 50%;
  border: 1px solid #cccccc;
}
.u-photo-no{
  background: #cccccc;
}
.user-info-sectwo {
  width: 100%;
  height: auto;
  margin-top: 0.14rem;
  background: #ffffff;
}
.user-info-sectwo > .sex-box {
  width: 100%;
  height: 0.49rem;
  border-bottom: 1px solid #ededed;
  line-height: 0.49rem;
  padding: 0 0.1rem;
  position: relative;
}
.sex-icon {
  position: absolute;
  display: inline-block;
  width: 0.19rem;
  height: 0.19rem;
  left: 0.10rem;
  top: 0.17rem;
  background: url("../images/sex@3x.png") no-repeat center;
  background-size: 0.19rem 0.19rem;
}
.sex-title {
  /*margin-left: 0.3rem;*/
  font-size: 16px;
  font-weight: 500;
  color: #2d2d2d;
}
.sex-setting {
  margin-left: 0.12rem;
  font-size: 14px;
  color: #9c9c9c;
}
.sex-box > .party-icon {
  width: 0.19rem;
  height: 0.19rem;
  background: url("../images/party-icon@3x.png") no-repeat center;
  background-size: 0.19rem 0.19rem;
}
.sex-box > .sing-icon {
  width: 0.21rem;
  height: 0.21rem;
  background: url("../images/sigin@3x.png") no-repeat center;
  background-size: 0.21rem 0.21rem;
}
.user-info-sectwo > .sing-box {
  border-bottom: none;
}
.sex-box > .company-icon {
  width: 0.21rem;
  height: 0.21rem;
  background: url("../images/company-icon@3x.png") no-repeat center;
  background-size: 0.21rem 0.21rem;
}
.sex-box > .phone-icon {
  width: 0.19rem;
  height: 0.21rem;
  background: url("../images/phone-icon@3x.png") no-repeat center;
  background-size: 0.19rem 0.21rem;
}
.sex-box > .qq-icon {
  width: 0.19rem;
  height: 0.21rem;
  background: url("../images/qq-icon@3x.png") no-repeat;
  background-size: 0.19rem 0.21rem;
}
.sex-box > .wechat-icon {
  width: 0.21rem;
  height: 0.21rem;
  background: url("../images/wechat@3x.png") no-repeat;
  background-size: 0.21rem 0.21rem;
}
.qq-setting {
  margin-left: 0.24rem;
}
.day{
  width: 14.2857143%;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  height: 34px;
  padding: 0;
  line-height: 34px;
  color: #000;
  background: #fff;
  vertical-align: middle;
}
.checked{
  background: #f50057;
  color: #fff!important;
  border-radius: 3px;
}

.personal-info-item3 {
  width: 100%;
  height: 0.45rem;
  margin-top: 0.2rem;
  padding: 0 0.45rem;
  margin-bottom: 0.3rem;
}
.btn-logout {
  width: 100%;
  height: 0.45rem;
  display: block;
  background: #FA6767;
  color: #ffffff;
  text-align: center;
  border-radius: 0.04rem;
  line-height: 0.45rem;
  font-size: 18px;
}
.touxiang-box>.touxiang-text{
  display: inline-block;
  height: 0.62rem;
  line-height: 0.62rem;
  font-size: 16px;
  font-weight: 500;
  color: #2d2d2d;
}
.u-photo-box>.touxiang-arrow-right{
  float: right;
  width: 0.08rem;
  height: 0.62rem;
  line-height: 0.62rem;
  display: inline-block;
  background: url(../images/arrow-right-two.png)center no-repeat;
  background-size: 0.08rem 0.16rem;
  margin-right: 0.05rem;
}



