
.newsdetail-layout {
  max-width: 7.5rem;
  min-width: 3.3rem;
  margin: 0 auto;
  height: 100%;
}
.newsdetail-header-bar {
  width: 100%;
  height: 0.4rem;
  text-align: center;
  background: #ffffff;
  position: fixed;
  overflow:hidden;
  top: 0;
  left: 0;
  z-index: 666;
  border-bottom: 1px solid #cccccc;
}
.newsdetail-header-bar .login-back-btn {
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat;
  margin: 0.1rem 0.1rem;
}
.newsdetail-header-bar .new-detail-t{
  height: 0.4rem;
  line-height: 0.4rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 16px;
  margin-top: 0;
  margin-left: 0;
}
.news-main-box {
  width: 100%;
  height: auto;
  background: #ffffff;
  padding: 0 0.12rem 0 0.12rem;
  position: relative;
}
.news-main-box > .news-detail-box {
  width: 100%;
  height: auto;
  background: #ffffff;
}
.tiezie-detail-header{
  width: 100%;
  /*height: 1rem;*/
  border-bottom: 1px solid #E7E7E7;

  background: #ffffff;
  padding: 10px;
  height: auto;
}
.tiezie-detail-header-bowser{
  top: 0.4rem;
  position: fixed;
  left: 0;
  z-index: 222;
}
.tiezie-detail-header-normal{
  top: 0;
}
.tiezie-detail-header > .mmnews-detail-title {
  font-size: 20px;
  color: #313131;
  width: 100%;
  /*height: 67px;*/
  max-height:67px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-overflow: -o-ellipsis-lastline;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  /*letter-spacing: 2px;*/
}
.tiezie-detail-header > .news-detail-tag {
  width: 100%;
  height: 0.4rem;
  overflow: hidden;
  /*  vertical-align: bottom;
  display: table-cell;*/
}
.top-newsdetail,.bottom-detail{
  width: 100%;
  height: 0.2rem;
  overflow: hidden;
}
.top-newsdetail{
  width: 284px;
}
.bottom-detail .publish-time {
  font-size: 12px;
  line-height: 0.3rem;
  overflow: hidden;
  color: #2d2d2d;
}
.bottom-detail .publish-name,
.bottom-detail .publish-tag {
  font-size: 12px;
  margin-left: 0.08rem;
  line-height: 0.3rem;
  overflow: hidden;
}
.bottom-detail .publish-name {
  color: #2d2d2d;
}

.top-newsdetail .publish-tag {
   width: 330px;
   overflow: hidden;
  color: #9c9c9c;
  height: 19px;
  display: block;
  font-size: 12px;
}
 .shouc-plus {
  position: absolute;
  right: 0;
  bottom: 0.2rem;
  margin-right: 0.12rem;
  border: 1px solid #20a0ff;
  border-radius: 0.04rem;
  width: 50px;
  height: 23px;
  color: #20a0ff;
  line-height: 23px;
  text-align: center;
  z-index: 22;
  background: #ffffff;
}
.para-one{
  height: auto;
  margin-top: 120px;
  font-size: 20px;
  line-height: 0.22rem;
  color: #474747;
  width: 100%;
  overflow: hidden;
  word-break: break-all;
  background-color: #fff;
}
.para-one-normal{
  margin-top: 60px;
}
.rich_media{
  max-width: 750px;
  width:100%;
}
.para-two {
  margin-top: 0.04rem;
}
.news-pic {
  height: 1.53rem;
  margin-top: 0.1rem;
  overflow: hidden;
}
.news-pic img{
  width: 100%;
  height: 1.53rem;
  overflow: hidden;
}
.news-read-zan {
  width: 100%;
  height: 0.4rem;
  margin-top: 0.2rem;
  background: #fff;
}
.news-read-zan > .news-read {
  display: inline-block;
  height: 0.4rem;
}
.news-read-zan > .news-read span {
  font-size: 14px;
  color: #9F9F9F;
}
.news-read-zan > .news-zan {
  display: inline-block;
  margin-left: 0.06rem;
  height: 0.4rem;
  position: relative;
}
.news-zan > .zan-icon {
  position: absolute;
  left: 0;
  top: 0.02rem;
  display: inline-block;
  width: 0.14rem;
  height: 0.16rem;
  background: url("../images/zan-icon.png") no-repeat;
}
.news-zan > .zan-num {
  font-size: 14px;
  color: #9F9F9F;
  margin-left: 0.18rem;
}
.ad-box {
  width: 100%;
  height: auto;
  padding: 0 0.12rem;
  background: #f3f3f3;
}
.ad-box > .ad-title {
  width: 100%;
  height: 0.3rem;
  position: relative;
  border-bottom: 1px dashed #E1E1E1;
}
.ad-title > .ad-name {
  text-align: center;
  width: 0.8rem;
  height: 0.3rem;
  position: absolute;
  top: 100%;
  left: 50%;
  line-height: 0.3rem;
  background: #F3F3F3;
  font-size: 16px;
  color: #898989;
  margin: -0.15rem 0 0 -0.4rem;
}
.ad-box > .ad-infos {
  width: 100%;
  height: auto;
  background: #fff;
  border: 2px solid #ccc;
  margin-top: 0.3rem;
}
.ad-link,.ad-link .adv-link-icon{
  display: block;
  width: 100%;
  height: auto;
}
.leave-msg {
  margin-top: 0.3rem;
  position: relative;
  margin-bottom: 0.5rem;
  background-color: #f3f3f3;
}
.leave-msg .write-msg {
  float: right;
  font-size: 14px;
  color: #6188B6;
  margin-right: 0.1rem;
  z-index: 44;
  position: absolute;
  right: 0;
  top: -20px;
}
.leave-msg-item {
  width: 100%;
  position: relative;
  margin-top: 10px;
  background-color: #f3f3f3;
  /*overflow: hidden;*/
}
.reply-msg-item{
  margin-top: 10px;
}
.leave-msg-item > .leave-msg-pic {
  display: inline-block;
  top: 0.04rem;
  width: 0.38rem;
  height: 0.38rem;
  line-height: 0.38rem;
  position: absolute;
}
.reply-msg-uname{
  height: 14px;
  margin-top: 8px;
  margin-bottom: 8px;
  display: block;
  padding-left: 10px;
  position: relative;
  position: relative;
}
.reply-msg-uname:before{
  content: '';
  height: 14px;
  width: 3px;
  background:#7ed321;
  position: absolute;
  left: 0;
  top: 0;
}
.leave-msg-news-r {
  display: inline-block;
  width: 80%;
  margin-left: 0.48rem;
  background-color: #f3f3f3;
}
.reply-msg-news-r{
  margin-top: 0;
  border-top: 1px solid #E1E1E1;
  width: 90%;
}
.leave-msg-news-r > .leave-msg-uname {
  display: block;
  color: #8E8E8E;
  font-size: 14px;
  width: 200px;
}
.leave-msg-news-r > .leave-msg-content {
  display: block;
  color: #525252;
  font-size: 16px;
  width: 300px;
  word-break: break-all;
}
.leave-msg-news-r > .leave-msg-time {
  display: block;
  color: #CECECE;
  font-size: 12px;

}


.news-black-line{
  width: 100%;
  height: 0.1rem;
  background: #585859;
}
.news-zan>.plus{
  color: #20A0FF;
  font-size: 16px;
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}
.news-share {
  font-size: 14px;
  border-radius: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  transform: translateY(100%);
  -webkit-transition: -webkit-transform 300ms;
  transition: transform 300ms;
  width: 100%;
  z-index: 1110;
}
.news-share-res,.news-share-jubao,.news-share-delete{
  width: 100%;
  height: 0.5rem;
  text-align: center;
  font-size:20px;
  color:#696969;
  line-height:0.5rem;
  background:#ffffff;
}
.news-share-res,.news-share-jubao{
  border-bottom: 1px solid #e7e7e7;
}
.news-modal-active {
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0)
}
.news-share-footer .news-share_btn {
  color:#696969;
  display: block;
  width: 100%;
  background-color: #F6F6F6;
  cursor: pointer;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
  padding: 0.625em 0;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}
.newsdetail-layout .news-sharebg {
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 0;
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 999;
  display: none;
}

.newsdetail-layout .news-sharebg-active {
  opacity: 1;
  display: block;
}
.news-main-box-bower{
  padding-top: 0.4rem;
}
.leave-msg-box{
  height: 140px;
  overflow: hidden;
  background: #f3f3f3;
}

.show-more-box{
  width: 100%;
  text-align: center;
  color: #48AFFC;
  font-size: 18px;
  height: 60px;
  background: #f3f3f3;
}
.show-more-text{
  display: block;
  position: relative;
  width: 200px;
  margin: 0 auto;
}
.show-more-text::before{
  content: "";
  background: url("../../assets/images/show-more-down.png") no-repeat center;
  background-size: 28px;
  width: 28px;
  height: 28px;
  position: absolute;
  left: 86px;
  top: 30px;
}
.look-container{
  width: 100%;
  height: auto;
  background: #fff;
  padding: 0 0.12rem;
}
.look-box{
  width: 200px;
  height: 0.3rem;
  position: relative;
  border-bottom: 1px dashed #E1E1E1;
  margin: 0 auto;
}
.look-box-text{
  text-align: center;
  height: 0.3rem;
  position: absolute;
  top: 100%;
  left: 50%;
  line-height: 0.3rem;
  background: #fff;
  font-size: 16px;
  color: #3D3D3D;
  margin: -0.15rem 0 0 -0.4rem;
  font-weight: 600;
}
.news-card-container{
  width: 100%;
  height: 200px;
  margin-top: 30px;
  position: relative;
  overflow: hidden;
}
.news-card-pic{
  width: 100%!important;
  height: 200px!important;
}
.news-card-bottom{
  width: 100%;
  height: 40px;
  background-color: #000000;
  opacity: 0.7;
  color: #ffffff;
  font-size: 18px;
  line-height: 40px;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.news-look-content-container{
  width: 100%;
  background-color: #fff;
  margin-bottom: 15px;
}
.news-look-content-container .news-item-box{
  width: 100%;
  height: 100%;
}
.news-item>.newsdetail-item-link{
  display: block;
  width: 100%;
  height: 1.1rem;
  border-top: 2px solid #E7E7E7;
  position: relative;
  padding:0.12rem;
  overflow: hidden;
}
.newsdetail-item-link>.newsdetail-item-icon{
  height: 0.85rem!important;
  width: 1.13rem!important;
  position: absolute!important;
  left: 0!important;
  top: 0.12rem!important;
}
.newsdetail-item-link>.news-info-container{
  margin-left: 1.13rem;
  height: 100%;
}
.news-info-container>.mmnews-detail-title{
  font-size: 18px;
  color: #000000;
  height: 50px;
  overflow: hidden;
  text-overflow: -o-ellipsis-lastline;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.news-info-container>.news_time_tag,.news-info-container>.news_comment_read{
  color: #9c9c9c;
  font-size: 12px;

}
.news-info-container>.news_time_tag{
  margin-top: 0.04rem;
  height: 14px;
  overflow: hidden;
}
.news-info-container>.news_time_tag>.news_time{
  /*display: inline-block;*/
  overflow: hidden;
  height: 14px;
}
.news_time_tag>.news_tag,.news_comment_read>.news_read{
  margin-left: 10px;
}
.news_time_tag>.news_tag{
  /*width: 120px;*/
  overflow: hidden;
  height: 14px;
  /*display: inline-block;*/
}
.news-card-link{
  width: 100%;
  height: 200px;
  overflow: hidden;
  display: block;
}
.back-home-container{
  width: 94%;
  height: 40px;
  text-align: center;
  line-height: 40px;
  color: #fff;
  font-size: 16px;
  margin: 10px auto;
  background-color: #48AFFC;
  border-radius: 4px;
}
.news-detail-content-mask{
  width: 100%;
  height: 300px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-image: -webkit-linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: -moz-linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: -o-linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: -webkit-repeating-linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: -moz-repeating-linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: -ms-repeating-linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: -o-repeating-linear-gradient( top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
  background-image: repeating-linear-gradient(top,rgba(255,255,255,.1) 0%,rgba(255,255,255,1) 100%);
}
.news-detail-more-container{
  width: 100px;
  height: 40px;
  margin: 260px auto 0 auto;
}
.news-detail-more-text{
  display: block;
  position: relative;
  width: 100px;
  margin: 0 auto;
  bottom: 20px;
  height: 16px;
  color: #48AFFC;
  font-size: 16px;
}
.news-detail-more-text::before{
  content: "";
  background: url("../../assets/images/show-more-down.png") no-repeat center;
  background-size: 28px;
  width: 28px;
  height: 28px;
  position: absolute;
  left: 34px;
  top: 30px;
}



