.newsdetail-layout {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
.data-content-topbar {
  width: 100%;
  height: 0.4rem;
  background-color: #3F3F3F;
  padding: 0.1rem 0  0 0.1rem;
  text-align: center;
  position: fixed;
  z-index: 1;
  overflow:scroll;
  top: 0;
  left: 0;
}
.data-content-topbar > .data-content-back,
.data-content-topbar > .data-content-more {
  display: block;
  width: 0.6rem;
  height: 0.4rem;
}
.data-content-topbar>.mint-button{
  border-radius: 0;
  border: 1px solid #3F3F3F;
}
.data-content-topbar > .data-content-back {
  background: url("../images/data-content-arrow.png") no-repeat;
  float: left;
  margin-top: 0.05rem;
  position: relative;
}
.data-content-topbar > .data-content-back::after {
  color: #ffffff;
  font-size: 14px;
  content: '返回';
  position: absolute;
  right: 0.15rem;
  top: -0.04rem;
}
.data-content-topbar > .data-content-more {
  background: url("../images/dat-content-more@3x.png") no-repeat;
  background-size: 0.25rem;
  float: right;
  width: 0.25rem;
  height: 0.4rem;
  margin-right: 0.1rem;
  margin-top: 0.06rem;
}
.tiezi-detai-main {
  width: 100%;
  height: 100%;
}
.tiezi-detai-main-bower{
  padding-top: 0.4rem;
}
.tiezi-detail-item1 {
  width: 100%;
  height: 0.66rem;
  border-bottom: 1px solid #E7E7E7;
  position: fixed;
  left: 0;
  z-index: 222;
  background: #ffffff;

}
.tiezi-detail-item1-bowser{
  top: 0.4rem;
}
.tiezi-detail-item1-normal{
  top: 0;
}
.tiezi-detail-item1 > .tiezi-detail-pic {
  width: 0.47rem;
  height: 0.47rem;
  border-radius: 50%;
  position: absolute;
  left: 0.09rem;
  top: 0.09rem;
}
.tiezi-detail-item1 > .tiezi-detail-infos {
  width: 100%;
  height: 0.66rem;
  margin-left: 0.68rem;
  padding-top: 0.1rem;
}
.tiezi-detail-infos > .tiezi-detail-name {
  font-size: 14px;
  color: #2D2D2D;
  height: 0.23rem;
  line-height: 0.23rem;
  font-weight: 600;
}
.tiezi-detail-infos > .tiezi-detail-date {
  height: 0.16rem;
  overflow: hidden;
  line-height: 0.16rem;
}

.tiezi-detail-date > .tiezi-detail-time {
  font-size: 12px;
  color: #3e3e3e;
}
.tiezi-detail-date > .tiezi-detail-tag {
  font-size: 12px;
  color: #9c9c9c;
  margin-left: 0.04rem;
  position: relative;
  height: 14px;
  overflow: hidden;
  display: inline-block;
  width: 108px;

}

.tiezi-detail-date > .tiezi-detail-shouc {
  display: block;
  width: 0.52rem;
  height: 0.25rem;
  line-height: 0.25rem;
  text-align: center;
  position: absolute;
  right: 0.1rem;
  top: 0.18rem;
  border: 1px solid #20a0ff;
  border-radius: 4px;
  color: #20A0FF;
  background: #ffffff;
  z-index: 22;
}
.tiezi-detail-item2 {
  width: 100%;
  height: auto;
  padding: 0 0.09rem;
  margin-top: 0.66rem;
}
.tiezi-detail-item2 > .tiezi-article-title {
  font-size: 18px;
  color: #2d2d2d;
  margin: 0.09rem 0;
}
.tiezi-item-contents {
  width: 100%;
  height: auto;
}
.tiezi-item-contents > .tiezi-item-content {
  font-size: 16px;
  color: #3e3e3e;
  width: 100%;
  overflow: hidden;
  word-break: break-all;
}
.tiezi-detail-item3 {
  width: 100%;
  height: 100%;
  /*padding: 0 0.09rem;*/
  margin-top: 0.06rem;
  margin-bottom: 0.4rem;
}
.tiezi-detail-allcom {
  border-bottom: 1px solid #d8d8d8;
  padding: 0 0.09rem;
}
.tiezi-detail-allcom > .tiezi-detail-num {
  border-bottom: 2px solid #20a0ff;
  font-size: 14px;
  color: #9c9c9c;
}
.tiezi-pinglun-item {
  width: 100%;
  /*padding: 0 0.09rem;*/
  /*margin-bottom: 0.1rem;*/
  height: auto;
  /*margin: 0 0.09rem 0.1rem 0.09rem;*/
}
.ul-detail{
  width: 100%;
  height: auto;

}
.inner-ul{
  background: #ffffff;

}
.tiezi-pinglun-item > .tiezi-pinglun-pic {
  width: 0.39rem;
  height: 0.39rem;
  border-radius: 50%;
  margin-top: 0.1rem;
  float: left;
  margin-left: 0.09rem;
}
.tiezi-pinglun-item > .tiezi-pinglun-infos {
  float: left;
  margin-left: 0.09rem;
  width: 80%;
  /*height: 0.69rem;*/
  overflow: hidden;
}
.tiezi-pinglun-infos > .tiezi-pinglun-name {
  width: 100%;
  height: 0.23rem;
  margin-top: 0.09rem;
  font-size: 14px;
  color: #20a0ff;
}
.tiezi-pinglun-infos > .tiezi-pinglun-time {
  font-size: 12px;
  color: #9c9c9c;
}
.tiezi-pinglun-infos > .tiezi-pinglun-huifu {
  font-size: 16px;
  color: #3e3e3e;
  display: block;
  width: 300px;
  word-break: break-all;
}
.my-line {
  background: #EDEEF0;
  width: 100%;
  height: 0.25rem;
}
.tiezi-detail-item4 {
  width: 100%;
  height: 0.45rem;
  position: fixed;
  border: 1px solid #cccccc;
  bottom: 0;
  left: 0;
  z-index: 5;
  background: #ffffff;
}
.tiezi-detail-item4 > .l-input {
  float: left;
  width: 1.34rem;
  height: 0.28rem;
}
.l-input > .input-content {
  display: block;
  width: 1.34rem;
  height: 0.28rem;
  font-size: 14px;
  margin-top: 0.09rem;
  background: url(../images/comment-icon.png) no-repeat center;
  margin-left: -20px;
  background-size:0.22rem 0.22rem ;
}
.input-content>.com-text{
  float: right;
  font-size: 16px;
  color: #9c9c9c;
  height: 0.45rem;
  line-height: 0.3rem;
  margin-right: 0.14rem;
}
.tiezi-detail-item4 > .r-tags {
  float: right;
  width: 50%;
  height: 0.45rem;
  font-size: 16px;
  color: #9c9c9c;
}
.r-tags > .r-fuhui-box {
  float: right;
  height: 0.45rem;
  line-height: 0.45rem;
  position: relative;
  padding-left: 0.3rem;
  right: 0;
}
.r-fuhui-box > .r-fuhui-pic {
  position: absolute;
  left: -0.2rem;
  top: 0.14rem;
}
.r-fuhui-box span{
  position: absolute;
  left: 0.05rem;
}
.r-tags > .r-zan-box {
  float: right;
  height: 0.45rem;
  line-height: 0.45rem;
  position: relative;
  margin-left: 0.3rem;
  margin-right: 0.1rem;
}
.r-zan-box>.plus{
  color: #20A0FF;
  font-size: 16px;
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}
.r-zan-box > .r-zan-pic {
  position: absolute;
  left: -0.2rem;
  top: 0.14rem;
}
.tiezi-detail-item4 > .tiezi-read {
  position: absolute;
  left: 50%;
  height: 0.45rem;
  line-height: 0.45rem;
  font-size: 14px;
  color: #9c9c9c;
}
.tiezi-read>.read-icon{
  display: inline-block;
  background: url(../images/eyes-read.png) center no-repeat;
  background-size: 0.2rem 0.2rem;
  width: 0.2rem;
  height: 0.2rem;
  position: absolute;
  left: 0.12rem;
  top: 0.12rem;
}
.tiezi-read>.tiezide-read-num{
    position: absolute;
    left: 0.34rem;
    top: 0;
    font-size: 16px;
    color: #9c9c9c;
}
