.sharebg-active {
  opacity: 1;
  display: block;
}
.black-line{
  width: 100%;
  height: 0.1rem;
  background: #585859;
}
.ch-share {
  font-size: 14px;
  border-radius: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  transform: translateY(100%);
  -webkit-transition: -webkit-transform 300ms;
  transition: transform 300ms;
  width: 100%;
  z-index: 1110;
}
.ch-share-res,.ch-share-jubao,.ch-share-delete{
  width: 100%;
  height: 0.5rem;
  text-align: center;
  font-size:20px;
  color:#696969;
  line-height:0.5rem;
  background:#ffffff;
}
.ch-share-res,.ch-share-jubao{
  border-bottom: 1px solid #e7e7e7;
}
.ch-modal-active {
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0)
}

.ch-modal-out {
  z-index: 1109;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  transform: translateY(100%)
}
