.sharebg {
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 0;
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 1100;
  display: none;
}

.news-share {
  font-size: 14px;
  border-radius: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  transform: translateY(100%);
  -webkit-transition: -webkit-transform 300ms;
  transition: transform 300ms;
  width: 100%;
  z-index: 1110;
}
.news-share-res,.news-share-jubao,.news-share-delete{
  width: 100%;
  height: 0.5rem;
  text-align: center;
  font-size:20px;
  color:#696969;
  line-height:0.5rem;
  background:#ffffff;
}
.news-share-res,.news-share-jubao{
  border-bottom: 1px solid #e7e7e7;
}
.news-modal-active {
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0)
}
.news-share-footer .news-share_btn {
  color:#696969;
  display: block;
  width: 100%;
  background-color: #F6F6F6;
  cursor: pointer;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
  padding: 0.625em 0;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}
.newsdetail-layout .news-sharebg {
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 0;
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 222;
  display: none;
}

.newsdetail-layout .news-sharebg-active {
  opacity: 1;
  display: block;
}
.tiezi-leave-msg-item {
  width: 100%;
  position: relative;
  margin-top: 10px;
  /*overflow: hidden;*/
}
.tiezi-reply-msg-item{
  margin-top: 10px;
}
.tiezi-leave-msg-item > .tiezi-leave-msg-pic {
  display: inline-block;
  top: 0.04rem;
  width: 0.38rem;
  height: 0.38rem;
  line-height: 0.38rem;
  position: absolute;
}
.tiezi-reply-msg-uname{
  height: 14px;
  margin-top: 8px;
  margin-bottom: 8px;
  display: block;
  padding-left: 10px;
  position: relative;
  position: relative;
}
.tiezi-reply-msg-uname:before{
  content: '';
  height: 14px;
  width: 3px;
  background:#7ed321;
  position: absolute;
  left: 0;
  top: 0;
}
.tiezi-leave-msg-r {
  display: inline-block;
  width: 80%;
  margin-left: 0.48rem;
  height: 100%;
  background: #FFFFFF;
}
.tiezi-reply-msg-r{
  margin-top: 0;
  border-top: 1px solid #E1E1E1;
  width: 90%;
  background: #FFFFFF;
}
.tiezi-leave-msg-r > .tiezi-leave-msg-uname {
  display: block;
  color: #8E8E8E;
  font-size: 14px;
}
.tiezi-leave-msg-r > .tiezi-leave-msg-content {
  display: block;
  color: #525252;
  font-size: 16px;
  width: 300px;
  word-break: break-all;
}
.tiezi-leave-msg-r > .tiezi-leave-msg-time {
  display: block;
  color: #CECECE;
  font-size: 12px;

}
.tiezi-item-content img{
  width: 100%;
  /*height: 2.5rem;*/
  height: auto;
}



