.index-layout{
    max-width: 7.5rem;
    min-width: 3.2rem;
    height: 100%;
    margin: 0 auto;
}
.my-swiper{
    height: 1.7rem;
}
.banner-text{
  display: inline-block;
  width: 100%;
  height: 0.5rem;
  position: absolute;
  left: 0;
  bottom: 0;
  color: #ffffff;
  font-weight: 500;
  font-size: 18px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding-left: 10px;
  background-color: #000;
  opacity: .7;
  z-index: 222;
}
.swiper-img{
    height: 1.8rem;
    width: 100%;
}
.index-layout .my-dot{
    text-align: right;
    padding-right: 0.2rem;
}
.index-header{
    width: 100%;
    height: 0.4rem;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    line-height: 0.4rem;
    align-items: center;
    border-bottom: 2px solid #D8D8D8;
    position: fixed;
    z-index: 111;
    top: 0;
    left: 0;
}
.index-header>.user-icon,.index-header>.search-icon{
    display: inline-block;
    float: left;
    width: 0.2rem;
    height: 0.4rem;
    background: url(../images/indexuser@3x.png)no-repeat center;
    background-size: 0.2rem 0.2rem;
    margin-left: 0.13rem;
}
.index-header>.search-icon{
    float: right;
    width: 0.2rem;
    height: 0.4rem;
    background: url(../images/index-search-icon@3x.png)no-repeat center;
    background-size: 0.2rem 0.2rem;
    margin-right: 0.13rem;
}
.index-header > .index-infos{
    flex-grow: 2;
    display: flex;
    justify-content: space-around;
    height: 0.4rem;
    margin-bottom: 0;
}
.index-infos>.head-item{
    align-self: center;
    float: left;
    padding: 0 0.20rem;
    display: inline-block;
    font-size:18px;
    color:#848a94;
    text-align:left;
    font-weight: 600;
}

.head-item.router-link-active{
    color: #20A0FF;
}

.tab-content-container{
    width: 100%;
    height: 100%;
    /*padding-top: 0.4rem;*/
}
.page-navbar>.mint-navbar{
    margin-bottom: 0.03rem;
    background: #F8F8F8;
}
.page-part>.mint-tab-item>.mint-tab-item-label{
    font-size:16px;
    color:#848a94;
}
.page-navbar>.page-part>.mint-tab-item.is-selected{
    font-size: 16px;
    color: #2d2d2d;
}
.news-all{
    width: 100%;
    /*height: 100%;*/
    position: relative;
}
.news-content{
    width: 100%;
    height: 100%;
    padding:0 0.12rem;
    background: #ffffff;
}

.news-content .news-item{
    width: 100%;
    height: 1.1rem;
}
.news-content .news-item-box{
    width: 100%;
    height: 100%;
}
.news-item>.news-item-link{
    display: block;
    width: 100%;
    height: 1.1rem;
    border-bottom: 2px solid #E7E7E7;
    position: relative;
    padding:0.12rem;
}
.news-item-link>.news-icon{
    height: 0.85rem;
    width: 1.13rem;
    position: absolute;
    left: 0;
    top: 0.12rem;
}
.news-item-link>.news-info-container{
    margin-left: 1.13rem;
    height: 100%;
}
.news-info-container>.news-title{
    font-size: 18px;
    color: #000000;
    max-height: 70px;
    height: auto;
    overflow: hidden;
    text-overflow: -o-ellipsis-lastline;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}
.news-info-container>.news_time_tag,.news-info-container>.news_comment_read{
    color: #9c9c9c;
    font-size: 12px;

}
.news-info-container>.news_time_tag{
    margin-top: 0.04rem;
    height: 14px;
    overflow: hidden;
}
.news-info-container>.news_time_tag>.news_time{
  /*display: inline-block;*/
  overflow: hidden;
  height: 14px;
}
.news_time_tag>.news_tag,.news_comment_read>.news_read{
    margin-left: 10px;
}
.news_time_tag>.news_tag{
    /*width: 120px;*/
    overflow: hidden;
    height: 14px;
  /*display: inline-block;*/
}
.index_news{
    background: #ffffff;
    width: 100%;
    height: 100%;
}
.news-all>.swiper-container{
    width: 100%;
    height: 1.8rem;
    margin-top: 0.4rem;
}
.news-all>.swiper-container>.mint-swipe-indicators{
    text-align: right;
    width: 100%;
}
.news-all>.swiper-container>.mint-swipe-indicators > .mint-swipe-indicator{
    background: #C8C8C8;
    opacity: 1;
}
.news-all>.swiper-container>.mint-swipe-indicators > .mint-swipe-indicator.is-active{
    background: #20A0FF;
    opacity: 1;
}
.back-top{
  position: fixed;
  right: 10px;
  bottom: 40px;
  width: 0.32rem;
  height: 0.32rem;
  background: url(../images/back_top.png) no-repeat;
  background-size: 0.32rem;
  /*display: none;*/
}
[v-cloak] {
  display: none !important;
}



