.personal_tieiz_layer{
  width: 100%;
  height: 100%;
}
.login-header-bar {
    width: 100%;
    height: 0.4rem;
    text-align: center;
    background: #ffffff;
    position: fixed;
    z-index: 1;
    overflow:scroll;
    top: 0;
    left: 0;
}
/*.personal-tiezi-box{
  padding-top: 0.4rem;
}*/
.login-header-bar .login-back-btn {
    float: left;
    width: 0.12rem;
    height: 0.19rem;
    display: inline-block;
    background: url("../images/back_login.png") no-repeat;
    margin: 0.1rem 0.1rem;
}
.login-header-bar .login-title {
    display: inline-block;
    font-size: 16px;
    line-height: 0.4rem;
    font-weight: 500;
    color: #2d2d2d;
}
.login-header-bar .topbar-left {
    float: right;
    font-size: 16px;
    color: #010101;
    line-height: 0.4rem;
    margin-right: 0.14rem;
}
.download-content-box {
    width: 100%;
    height: 100%;
    background: #ffffff;
}
.tiezi-read-box>.read-icon{
  display: inline-block;
  background: url(../images/eyes-read.png) center no-repeat;
  background-size: 0.2rem 0.2rem;
  width: 0.2rem;
  height: 0.2rem;
  position: absolute;
  left: 0.12rem;
  top: 0.1rem;
}
.tiezi-item {
    width: 100%;
    height: 100%;
}
.tiezi-item > .line {
    width: 100%;
    height: 0.1rem;
    background: #EDEEF0;
}
.tiezi-item > .who-tiezi-item1 {
    width: 100%;
    height: 0.82rem;
    position: relative;
}
.who-tiezi-item1 > .pc-tiezi-pic {
    position: absolute!important;
    left: 0.07rem!important;
    top: 0.12rem!important;
    width: 0.66rem!important;
    height: 0.66rem!important;
    border-radius: 50%!important;
}
.who-tiezi-item1 > .who-publish-box {
    margin-left: 0.88rem;
    height: 0.82rem;
    padding-top: 0.2rem;
}
.who-publish-box > .who-publish-name {
    font-size: 14px;
    color: #000000;
    height: 0.23rem;
    line-height: 0.23rem;
}
.who-publish-box > .who-publish-times {
    font-size: 12px;
    color: #9a9a9a;
}
.who-publish-times > .who-publish-time {
    margin-right: 0.11rem;
}
.who-tiezi-item1 > .delete-tiezi {
    width: 0.52rem;
    height: 0.25rem;
    border: 1px solid #dfdfdf;
    border-radius: 0.04rem;
    position: absolute;
    right: 0.13rem;
    top: 0.13rem;
    text-align: center;
    color: #a9a9a9;
    font-size: 12px;
    line-height: 0.25rem;
}
.who-tiezi-item2 {
    width: 100%;
    height: 0.92rem;
}
.who-tiezi-item2 > .who-tiezi-info {
    width: 100%;
    height: 0.96rem;
    display: block;
    padding: 0 0.13rem;
}
.who-tiezi-info > .line-one {
    background: #F5F5F5;
    width: 100%;
    height: 1px;
}
.who-tiezi-info > .tiezi-title {
    height: 0.23rem;
    line-height: 0.23rem;
    font-size: 14px;
    color: #000000;
    font-weight: 600;
}
.who-tiezi-info > .r-tiezi-content {
    font-size: 12px;
    color: #474747;
    padding-right: 0.08rem;
    height: 66px;
    overflow: hidden;
    text-overflow:ellipsis;
    -o-text-overflow:ellipsis;

}
.who-tiezi-item3 {
    width: 100%;
    height: 0.4rem;
}
.who-tiezi-item3 > .tiezi-read-box,
.who-tiezi-item3 > .tiezi-zanfh-box {
    width: 50%;
    height: 0.4rem;
    font-size: 14px;
    color: #9c9c9c;
    padding: 0.12rem 0.18rem;
}
.who-tiezi-item3 > .tiezi-read-box {
    float: left;
  position: relative;
}
.tiezi-read-box>.tiezi-read-num{
  position: absolute;
  left: 0.34rem;
  top: 0.11rem;
}
.who-tiezi-item3 > .tiezi-zanfh-box {
    float: right;
}
.tiezi-zanfh-box > .tiezi-hui-box {
    height: 14px;
    float: left;
    position: relative;
}
.tiezi-hui-box > .tiezi-hui-icon {
    display: inline-block;
    width: 0.14rem;
    height: 0.14rem;
    background: url("../images/tiezi-huifu@3x.png") no-repeat;
    background-size: 0.14rem;
    position: absolute;
    left: -0.18rem;
    top: 0.03rem;
}
.tiezi-zanfh-box > .tiezi-zan-box {
    float: right;
    position: relative;
}
.tiezi-zan-box > .tiezi-zan-icon {
    display: inline-block;
    width: 0.15rem;
    height: 0.17rem;
    background: url("../images/tiezi-zan@3x.png") no-repeat;
    background-size: 0.15rem;
    position: absolute;
    left: -0.18rem;
    top: 0.01rem;
}
.send-tiezi{
  float: right;
  height: 0.4rem;
  vertical-align: middle;
  line-height: 0.4rem;
  padding: 0 0.1rem;
  font-size: 16px;
}



