.personal_zan_layer {
  width: 100%;
  height: 100%;
}
.zan-header-bar{
  box-shadow: 1px 1px 2px #ccc;
}
.login-header-bar {
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 0.4rem;
  text-align: center;
  background: #ffffff;
  border-bottom: 1px solid #EDEEF0;
  overflow:scroll;
  top: 0;
  left: 0;
}
.login-header-bar .login-back-btn {
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat;
  margin: 0.1rem 0.1rem;
}
.login-header-bar .login-title {
  display: inline-block;
  font-size: 16px;
  height: 0.4rem;
  line-height: 0.4rem;
  font-weight: 500;
  color: #2d2d2d;
}
.login-header-bar .topbar-left {
  float: right;
  font-size: 16px;
  color: #010101;
  line-height: 0.4rem;
  margin-right: 0.14rem;
}
.zan-box {
  width: 100%;
  height: 100%;
  background: #ffffff;
  padding-top: 0.4rem;
}
.zan-box .who-zan-item {
  width: 100%;
  height: 1.98rem;
}
.who-zan-item > .who-zan-top {
  width: 100%;
  height: 0.9rem;
}
.who-zan-top > .who-zan-names {
  width: 100%;
  height: 0.5rem;
  position: relative;
}
.who-zan-names > .who-zan-pc {
  width: 0.5rem!important;
  height: 0.5rem!important;
  border-radius: 50%!important;
  position: absolute!important;
  left: 0.12rem!important;
  top: 0.06rem!important;
  display: inline-block!important;
}
.who-zan-names > .who-zan-infos {
  display: inline-block;
  width: 100%;
  height: 0.5rem;
  padding-left: 0.66rem;
}
.who-zan-infos > .who-zan-name {
  font-size: 16px;
  color: #010101;
  margin-top: 0.1rem;
}
.who-zan-infos > .who-zan-time {
  font-size: 12px;
  color: #9a9a9a;
  margin-top: 0.06rem;
}
.zan-w {
  width: 100%;
  height: 0.4rem;
  line-height: 0.4rem;
  padding-top: 0.1rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.zan-w > .zan-article {
  margin-left: 0.12rem;
  font-size: 16px;
  color: #080808;
  display: inline-block;
  float: left;
  overflow: hidden;
  height: 0.4rem;
  padding-right: 0.2rem;
  width: 100%;
}
.zan-w > .zan-icon {
  display: inline-block;
  width: 0.26rem;
  height: 0.4rem;
  background: url("../images/zan.png") no-repeat center;
}
.who-zan-item > .who-zan-bottom {
  width: 100%;
  height: 0.9rem;
  background: #F6F6F6;
}
.who-zan-bottom > .who-zan-article {
  display: block;
  width: 100%;
  height: 0.9rem;
  position: relative;
  margin-top: 0.1rem;
}
.who-zan-article > .article-pic {
  width: 0.88rem;
  height: 0.88rem;
  position: absolute;
  left: 0.12rem;
  top: 0;
}
.who-zan-article > .article-content-box {
  width: 100%;
  height: 100%;
  padding-left: 1.1rem;
  padding-top: 0.13rem;
}
.article-content-box > .article-title {
  font-size: 14px;
  color: #080808;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.article-content-box > .article-zan-content {
  font-size: 12px;
  color: #9a9a9a;
  margin-top: 0.1rem;
  height: 32px;
  overflow: hidden;
  padding-right: 0.2rem;
}



