.personal-top-bar {
  position: fixed;
  z-index: 222;
  width: 100%;
  height: 0.4rem;
  background: #ffffff;
  text-align: center;
  overflow:scroll;
  top: 0;
  left: 0;
}
.personal-top-bar .login-back-btn {
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat center;
  margin: 0.1rem;
}
.personal-top-bar .personal-center {
  display: inline-block;
  font-size: 16px;
  line-height: 0.4rem;
  height: 0.4rem;
  font-weight: 600;
  color: #2d2d2d;
}
.personal-top-bar .topbar-left {
  float: right;
  font-size: 16px;
  color: #010101;
  line-height: 0.4rem;
  margin-right: 0.14rem;
}
.main-personal-info {
  width: 100%;
  height: auto;
  padding: 0 0.1rem 0.1rem 0;
  background: #ffffff;
}
.personal-line{
  height: 15px;
  width: 100%;
  background: #EDEEF0;
  padding-top: 0.6rem;
}
.main-personal-info > .personal-info-item1 {
  width: 100%;
  height: 0.92rem;
  padding: 0.1rem 0.13rem;
}
.personal-info-item1 > .p-item-box {
  float: left;
  width: 80%;
  height: 0.69rem;
  position: relative;
}
.p-item-box > .user-icons {
  width: 0.69rem;
  height: 0.69rem;
  border-radius: 50%;
  display: inline-block;
}
.p-item-box > .user-name-sing,.p-item-box>.user-name-head {
  display: inline-block;
  height: 0.69rem;
  position: absolute;
  left: 0.80rem;
  top: 0.1rem;
}
.p-item-box>.user-name-head >.user-sign-login{
  line-height: 0.6rem;
  margin-left: 0.2rem;
}
.p-item-box > .user-name-sing p {
  height: 0.22rem;
  line-height: 0.22rem;
  font-size: 16px;
  color: #0B0B0B;
}
.personal-info-item1 > .p-item-arrow {
  float: right;
  width: 20%;
  height: 0.69rem;
  position: relative;
}
.p-item-arrow > .us-arrow-right {
  display: block;
  width: 0.08rem;
  height: 0.16rem;
  background: url("../images/arrow-right.png") no-repeat;
  position: absolute;
  right: 0;
  top: 0.25rem;
}
.personal-info-item2 {
  margin-top: 0.2rem;
  width: 100%;
  height: auto;
  background: #ffffff;
  padding: 0 0.25rem 0 0.16rem;
}
.item2 {
  width: 100%;
  height: 0.5rem;
}
.item2 > .item2-link {
  width: 100%;
  height: 0.5rem;
  display: block;
}
.item2-link > .l-item2 {
  float: left;
  width: 0.3rem;
  height: 0.5rem;
  line-height: 0.5rem;
  padding: 0.1rem 0;
}
.l-item2 > .my-s-icon {
  width: 0.26rem;
  height: 0.26rem;
}
.pcen-r-ietm {
  width: 100%;
  height: 0.5rem;
  padding: 0.14rem 0;
  border-bottom: 1px solid #F5F5F5;
}
.pcen-r-ietm > .my-about {
  font-size: 18px;
  height: 0.22rem;
  color: #2D2D2D;
  float: left;
  margin-right: 0.12rem;
  font-weight: normal;
}
.pcen-r-ietm > .my-arrow-rightbox {
  float: right;
  display: block;
  width: 0.09rem;
  height: 0.5rem;
  /*height: 0.16rem;*/
  line-height: 0.5rem;
  background: url("../images/arrow-right-two.png") no-repeat;
  background-size: 0.09rem;
}
.personal-info-item3 {
  width: 100%;
  height: 0.45rem;
  margin-top: 0.2rem;
  padding: 0 0.45rem;
  margin-bottom: 0.3rem;
}
.btn-logout {
  width: 100%;
  height: 0.45rem;
  display: block;
  background: #FA6767;
  color: #ffffff;
  text-align: center;
  border-radius: 0.04rem;
  line-height: 0.45rem;
  font-size: 18px;
}
.unloginpersonal-info-item3 {
  width: 100%;
  height: 0.45rem;
  margin-top: 0.2rem;
  padding: 0 0.45rem;
  margin-bottom: 1rem;
}
.unloginbtn-logout,
.unloginbtn-register {
  width: 100%;
  height: 0.45rem;
  display: block;
  text-align: center;
  border-radius: 0.04rem;
  line-height: 0.45rem;
  font-size: 18px;
}
.unloginbtn-logout {
  background: #20A0FF;
  color: #ffffff;
}
.unloginbtn-register {
  color: #20a0ff;
  border: 0.03rem solid #20a0ff;
  border-radius: 0.04rem;
  margin-top: 0.1rem;
}



