.get-jifen-layout{
  max-width: 7.5rem;
  min-width: 3.2rem;
  height: 100%;
  margin: 0 auto;
  background: #f4f4f4;
}
.pcen-jifen-bar {
  width: 100%;
  height: 0.4rem;
  background: #20A0FF;
  position: fixed;
  z-index: 111;
  color: #ffffff;
  top: 0;
  left: 0;
}
.pcen-jifen-bar > .jifen-back-btn {
  display: inline-block;
  width: 0.14rem;
  height: 0.2rem;
  background: url("../images/jifen-back-icon.png") no-repeat;
  margin: 0.12rem 0.12rem;
}
.pcen-jifen-bar > .jifen-title {
  text-align: center;
  display: inline-block;
  height: 0.22rem;
  width: 2rem;
  line-height: 0.22rem;
  font-size: 16px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -0.11rem 0 0 -1rem;
}
.pcen-jifen-bar > .jifen-detail-text {
  float: right;
  font-size: 14px;
  margin-right: 0.26rem;
  margin-top: 0.11rem;
}
.jifen-get-main{
  width: 100%;
  height: 100%;
  padding: 0.4rem 0.12rem 0.2rem 0.12rem;
}
.jifen-get-main>.jifen-info{
  width: 100%;
  margin-top: 0.12rem;
  height: auto;
  line-height: 0.24rem;
  font-size: 16px;
  box-shadow: 4px 4px 5px #888888;
  background-color: #20A0FF;
  color: #FFFFFF;
  border-radius: 0 20px;
  padding-left: 0.06rem;
}



