.index_tiezi{
  max-width: 7.5rem;
  min-width: 3.2rem;
  height: 100%;
  margin: 0 auto;
  background: #ffffff;
}
/*.tiezi-c-box {
    width: 100%;
    height: 100%;
    background: #ffffff;
}*/
.tiezi-cc-box {
  width: 100%;
  height: 100%;
  background: #ffffff;
  padding-top: 0.4rem;
  position: relative;
}
.tiezi-item {
    width: 100%;
    height: 2.6rem;
}

.tiezi-item > .line {
    width: 100%;
    height: 0.1rem;
    background: #EDEEF0;
}
.tiezi-item:first-of-type >.line{
  height: 0;
}
.tiezi-item > .who-tiezi-item1 {
    width: 100%;
    height: 0.82rem;
    position: relative;
}
.who-tiezi-item1 > .tiezi-pic {
    position: absolute!important;
    left: 0.07rem!important;
    top: 0.12rem!important;
    width: 0.66rem!important;
    height: 0.66rem!important;
    border-radius: 50%!important;
    border: 1px solid #cccccc!important;
}
.who-tiezi-item1 > .who-publish-box {
    margin-left: 0.88rem;
    height: 0.82rem;
    padding-top: 0.2rem;
    overflow: hidden;
}
.who-publish-box > .who-publish-name {
    font-size: 14px;
    color: #000000;
    height: 0.23rem;
    line-height: 0.23rem;
}
.who-publish-box > .who-publish-times {
    font-size: 12px;
    color: #9a9a9a;
}
.who-publish-times > .who-publish-time {
    margin-right: 0.11rem;
}
.who-tiezi-item1 > .index-shouchang-tiezi {
    width: 0.52rem;
    height: 0.25rem;
    border-radius: 0.04rem;
    position: absolute;
    right: 0.13rem;
    top: 0.13rem;
    text-align: center;
    font-size: 12px;
    line-height: 0.25rem;
    background:#ffffff;
    border:1px solid #20a0ff;
    color: #20a0ff;
}
.who-tiezi-item2 {
    width: 100%;
    height: 1rem;
}
.who-tiezi-item2 > .who-tiezi-info {
    width: 100%;
    height: 1.0rem;
    display: block;
    padding: 0 0.13rem;
}

.who-tiezi-info > .index-tiezi-title {
    height: 0.4rem;
    line-height: 0.2rem;
    font-weight: 600;
    font-size:18px;
    color:#2d2d2d;
    margin-top: 8px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.who-tiezi-info > .index-tiezi-content {
    font-size: 12px;
    color: #474747;
    padding-right: 0.08rem;
    height: 68px;
    overflow: hidden;
    text-overflow:ellipsis;
    -o-text-overflow:ellipsis;
}
.who-tiezi-item3 {
    width: 100%;
    height: 0.4rem;
    margin-top: 0.2rem;
    border-top: 1px solid #F5F5F5;
}
.who-tiezi-item1 > .delete-tiezi {
  width: 0.52rem;
  height: 0.25rem;
  border: 1px solid #dfdfdf;
  border-radius: 0.04rem;
  position: absolute;
  right: 0.13rem;
  top: 0.13rem;
  text-align: center;
  color: #a9a9a9;
  font-size: 12px;
  line-height: 0.25rem;
}
.who-tiezi-item3 > .tiezi-read-box,
.who-tiezi-item3 > .tiezi-zanfh-box {
    width: 50%;
    height: 0.4rem;
    font-size: 14px;
    color: #9c9c9c;
    padding: 0.12rem 0.18rem;
}
.who-tiezi-item3 > .tiezi-read-box {
    float: left;
    position: relative;
}
.tiezi-read-box>.read-icon{
    display: inline-block;
    background: url(../images/eyes-read.png) center no-repeat;
    background-size: 0.2rem 0.2rem;
    width: 0.2rem;
    height: 0.2rem;
    position: absolute;
    left: 0.12rem;
    top: 0.1rem;
}
.tiezi-read-box>.tiezi-read-num{
    position: absolute;
    left: 0.34rem;
    top: 0.11rem;
}
.who-tiezi-item3 > .tiezi-zanfh-box {
    float: right;
}
.tiezi-zanfh-box > .tiezi-hui-box {
    height: 14px;
    float: left;
    position: relative;
}
.tiezi-hui-box > .tiezi-hui-icon {
    display: inline-block;
    width: 0.15rem;
    height: 0.15rem;
    background: url("../images/tiezi-huifu@3x.png") no-repeat;
    background-size: 0.15rem 0.15rem;
    position: absolute;
    left: -0.18rem;
    top: 0.03rem;
}
.tiezi-zanfh-box > .tiezi-zan-box {
    float: right;
    position: relative;
}
.tiezi-zan-box > .tiezi-zan-icon {
    display: inline-block;
    width: 0.15rem;
    height: 0.17rem;
    background: url("../images/tiezi-zan@3x.png") no-repeat center;
    background-size: 0.15rem 0.17rem;
    position: absolute;
    left: -0.18rem;
    top: 0.01rem;
}
.tiezi-content-layout{
  width: 100%;
  height: 100%;

  background: #ffffff;
}
.tiezi-cc-box>.swiper-container{
  width: 100%;
  height: 1.7rem;
  margin-top: 0.2rem;
}
.tiezi-cc-box>.swiper-container>.mint-swipe-indicators{
  text-align: right;
  width: 100%;
}
.tiezi-cc-box>.swiper-container>.mint-swipe-indicators > .mint-swipe-indicator{
  background: #C8C8C8;
  opacity: 1;
}
.tiezi-cc-box>.swiper-container>.mint-swipe-indicators > .mint-swipe-indicator.is-active{
  background: #20A0FF;
  opacity: 1;
}

.send-tie-box{
  width: 0.6rem;
  height: 0.6rem;
  background: #20A0FF;
  position: fixed;
  bottom: 0.2rem;
  right: 0;
  border-radius: 0.04rem;
  padding: 0.1rem;
  text-align: center;
  opacity: .6;
}
.tie-send-plus{

}
.plus-icon{
  width: 20px;
  height: 20px;
}
.tie-send-text{
  color: #ffffff;
  font-size: 16px;
}



