
.pcen-layout {
  width: 100%;
  height: 100%;
}
.pcen-jifen-bar {
  width: 100%;
  height: 0.4rem;
  background: #20A0FF;
  position: fixed;
  z-index: 111;
  color: #ffffff;
  top: 0;
  left: 0;
}
.pcen-jifen-bar > .jifen-back-btn {
  display: inline-block;
  width: 0.14rem;
  height: 0.2rem;
  background: url("../images/jifen-back-icon.png") no-repeat;
  margin: 0.12rem 0.12rem;
}
.pcen-jifen-bar > .jifen-title {
  text-align: center;
  display: inline-block;
  height: 0.22rem;
  width: 2rem;
  line-height: 0.22rem;
  font-size: 16px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -0.11rem 0 0 -1rem;
}
.pcen-jifen-bar > .jifen-detail-text {
  float: right;
  font-size: 14px;
  margin-right: 0.26rem;
  margin-top: 0.11rem;
}
.jifen-item1 {
  background: #20A0FF;
  width: 100%;
  height: 1.5rem;
  padding: 0.4rem 0.44rem 0.24rem 0.4rem;
}
.jifen-item1 > .jifen-alltotal-box {
  width: 100%;
  height: 0.9rem;
  text-align: center;
}
.jifen-alltotal-box > .jifen-alltotal {
  font-size: 64px;
  color: #ffffff;
}
.jifen-item2 {
  width: 100%;
  height: auto;
  padding: 0 0.1rem;
}
.jifen-sm-item {
  border-bottom: 1px solid #DFDFDF;
  width: 100%;
  height: 0.45rem;
}
.jifen-sm-item > .jifen-get-t,
.jifen-sm-item > .jifen-get-num {
  line-height: 0.45rem;
  font-size: 16px;
}
.jifen-sm-item > .jifen-get-t {
  float: left;
  color: #000000;
}
.jifen-sm-item > .jifen-get-num {
  float: right;
  color: #5CA80A;
}
.jifen-sm-item > .jifen-use-num {
  color: #E20000;
}
.jifen-item3 {
  width: 100%;
  height: 0.4rem;
  text-align: left;
}
.jifen-item3 > .how-get-jifen {
  color: #20a0ff;
  float: right;
  font-size: 14px;
  margin: 0.2rem 0.1rem;
  position: relative;
}
.jifen-item3 > .how-get-jifen::before {
  content: '？';
  background: #20a0ff;
  width: 0.16rem;
  height: 0.16rem;
  border-radius: 50%;
  color: #ffffff;
  position: absolute;
  left: -0.22rem;
  top: 0.02rem;
  text-align: center;
  padding-left: 0.05rem;
}



