.forget-layer {
  width: 100%;
  height: 100%;
  background: #ffffff;
}
.forget-title-box {
  border-bottom: 1px solid #DBDCDE;
  text-align: center;
  height: 0.4rem;
}
.forget-title-box>.forget-back{
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat center;
  margin: 0.1rem;
}
.forget-title-box .forget-title {
  line-height: 0.4rem;
  height: 0.4rem;
  margin: 0.04rem auto 0.09rem auto;
  font-size: 16px;
}
.forget-info-box {
  width: 100%;
  height: 100%;
  padding: 0.2rem 0.44rem;
}
.forget-info-box div {
  width: 100%;
  height: 0.4rem;
  text-align: center;
  margin-top: 0.2rem;
}
.forget-info-box div input {
  width: 100%;
  height: 0.38rem;
  border: 1px solid #d6d6d6;
  border-radius: 0.04rem;
  padding: 0.16rem 0.17rem;
  font-size: 16px;
}
.forget-code {
  position: relative;
}
.forget-code  .forgetg-code {
  width: 1rem;
  position: absolute;
  right: 0.32rem;
  bottom: 0.09rem;
  border: 1px solid #20a0ff;
  border-radius: 0.04rem;
  color: #20a0ff;
}
.forget-info-box > .forget-btn {
  margin-top: 0.2rem;
}
.forget-info-box > .forget-btn .forget-btn-info {
  width: 100%;
  height: 0.38rem;
  border: 1px solid #d6d6d6;
  border-radius: 0.04rem;
  line-height: 0.38rem;
  display: block;
  background: #20a0ff;
  text-align: center;
  color: #ffffff;
  font-size: 18px;
}



