.search-layout{
    max-width: 7.5rem;
    min-width: 3.2rem;
    height: 100%;
    margin: 0 auto;
}
.search-layout>.search-header{
    height: 0.4rem;
    width: 100%;
    position: fixed;
    line-height: 0.4rem;
    border-bottom: 2px solid #DDDDDD;
    background: #ffffff;
    top: 0;
    left: 0;
    z-index: 999;
}
.search-header>.search-cancel,.search-header>.search-ok{
    font-size:18px;
    width: 10%;
    font-weight: 600;
}
.search-header>.search-cancel{
    float: left;
    color:#848a94;
    margin-left: 0.09rem;
}
.search-header>.search-ok{
    color:#20a0ff;
    margin-right: 0.11rem;
    position: absolute;
    right: 0;
    top: 0 ;
    margin-left: 0.11rem;
    text-align: right;
    z-index: 333;
    height: 0.4rem;
    line-height: 0.4rem;
    font-weight: 600;
}
.search-header>.search-content{
    width: 74%;
    height: 0.34rem;
    margin: 0 0.09rem;
    border-radius:4px;
    position: absolute;
    overflow: hidden;
    left: 0.43rem;
    top: 0.02rem;
    background: #f8f8f8;
}
.search-content>.search-result{
    position: absolute;
    z-index: 333;
    left: 20px;
    top: 0;
    height: 40px;
    padding-top: 8px;
    width: 100%;
}
.search-result>.search-result-item{
    width: auto;
    height: 20px;
    background:#d8d8d8;
    border-radius:4px;
    text-align: center;
    line-height: 20px;
    color: #ffffff;
    font-size: 14px;
    margin-left: 8px;
    padding: 0 6px 0 4px;
}
.search-result li{
    float: left;
}
.search-content>.search-key{
    display: inline-block;
    height:0.34rem;
    width: 80%;
    margin-left: 0.38rem;
    font-size: 16px;
    background: #f8f8f8;
    color: #000000;
    position: absolute;
}
.search-content>.search-icon{
    position: absolute;
    display: inline-block;
    left: 0.07rem;
    top: 0.07rem;
    width: 0.19rem;
    height: 0.19rem;
    background: url(../images/search_page_icon.png) no-repeat;
}

.search-content-layout{
    padding-top: 0.4rem;
    width: 100%;
    height: 100%;
}
.search-all{
    width: 100%;
    background: #ffffff;
    /*margin-top: 5px;*/
    padding: 10px;
    margin-top: 40px;
    z-index: 999;
}
.search-all-box{
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap:wrap;
    height: auto;
    padding: 10px ;
}
.search-all-box>.search-item{
    border:1px solid #d5d5d5;
    border-radius:2px;
    padding: 0 0.1rem;
    height:29px;
    text-align: center;
    line-height: 26px;
    font-size: 16px;
    margin-bottom: 10px;

}
.search-all-box>.search-item.actived{
    border:1px solid #20a0ff;
    color: #20a0ff;
}

.disabled{
    pointer-events: none;
}
.mint-navbar{
  border-bottom: 1px solid #cccccc;
  height: 0.5rem;
  position: fixed;
  left: 0;
  top:0.4rem;
  width: 100%;
  z-index: 888;
}
.mint-navbar .mint-tab-item{
  height: 0.5rem;
}
.mint-navbar .mint-tab-item.is-selected{
  border-bottom:4px solid #20a0ff;
  padding-bottom: -9px;
}
.mint-navbar .mint-tab-item .mint-tab-item-label{
  font-size:16px;
  color:#848a94;
  height: 0.16rem;
  overflow: hidden;
}
.mint-navbar .mint-tab-item.is-selected .mint-tab-item-label{
  color:#20a0ff;
}
.news-content{
  width: 100%;
  height: 100%;
  padding:0 0.12rem;
  background: #ffffff;
}

.news-content .news-item{
  width: 100%;
  height: 1.1rem;
  overflow: hidden;
}
.news-content .news-item-box{
  width: 100%;
  height: 100%;
}
.news-item>.news-item-link{
  display: block;
  width: 100%;
  height: 1.1rem;
  border-bottom: 2px solid #E7E7E7;
  position: relative;
  padding:0.12rem;
  color: #000;
}
.news-item-link>.news-icon{
  height: 0.85rem;
  width: 1.13rem;
  position: absolute;
  left: 0;
  top: 0.12rem;
}
.news-item-link>.news-search-container{
  height: 100%;
}
.news-search-container>.news-title{
  font-size: 18px;
  color: #000000;
  height: 0.2rem;
  overflow: hidden;
  line-height: 0.2rem;
}
.news-search-content{
  width: 100%;
  height: 0.4rem;
  overflow: hidden;
  line-height: 0.2rem;
  margin-bottom: 0.1rem;
  margin-top: 0.1rem;
}
.news-search-container>.news_time_tag,.news-search-container>.news_comment_read{
  color: #9c9c9c;
  font-size: 12px;

}
.news-search-container>.news_time_tag{
  margin-top: 0.04rem;
  height: 14px;
}
.news-search-container>.news_time_tag>.news_time{
  width: 60px;
  display: inline-block;
  overflow: hidden;
  height: 14px;
}
.news_time_tag>.news_tag,.news_comment_read>.news_read{
  margin-left: 10px;
}
.news_time_tag>.news_tag{
  width: 140px;
  overflow: hidden;
  height: 14px;
  display: inline-block;
}
.index_news{
  background: #ffffff;
  width: 100%;
  height: 100%;
}
.news-all>.swiper-container{
  width: 100%;
  height: 1.7rem;
}
.swipe{
  width: 100%;
  height: 100%;
}
.tiezi-content-box {
  width: 100%;
  height: 100%;
  background: #ffffff;
  position: relative;
  /*overflow-y: scroll*/
  /*padding-top: 0.4rem;*/
}
.tiezi-search-item {
  width: 100%;
  height: 1rem;
  overflow: hidden;
}
.tiezi-search-item > .line {
  width: 100%;
  height: 0.1rem;
  background: #EDEEF0;
}
.tiezi-search-item > .who-tiezi-item1 {
  width: 100%;
  height: 0.82rem;
  position: relative;
}
.who-tiezi-item1 > .tiezi-pic {
  position: absolute;
  left: 0.07rem;
  top: 0.12rem;
  width: 0.66rem;
  height: 0.66rem;
  border-radius: 50%;
  border: 1px solid #cccccc;
}
.who-tiezi-item1 > .who-publish-box {
  margin-left: 0.88rem;
  height: 0.82rem;
  padding-top: 0.2rem;
  overflow: hidden;
}
.who-publish-box > .who-publish-name {
  font-size: 14px;
  color: #000000;
  height: 0.23rem;
  line-height: 0.23rem;
}
.who-publish-box > .who-publish-times {
  font-size: 12px;
  color: #9a9a9a;
}
.who-publish-times > .who-publish-time {
  margin-right: 0.11rem;
}
.who-tiezi-item1 > .index-shouchang-tiezi {
  width: 0.52rem;
  height: 0.25rem;
  border-radius: 0.04rem;
  position: absolute;
  right: 0.13rem;
  top: 0.13rem;
  text-align: center;
  font-size: 12px;
  line-height: 0.25rem;
  background:#ffffff;
  border:1px solid #20a0ff;
  color: #20a0ff;
}
.tiezi-search-item2 {
  width: 100%;
  height: 1rem;
}
.tiezi-search-item2 > .search-tiezi-info {
  width: 100%;
  height: 1.0rem;
  display: block;
  padding: 0 0.13rem;
}

.search-tiezi-info > .index-tiezi-title {
  height: 0.4rem;
  line-height: 0.2rem;
  font-weight: 600;
  font-size:18px;
  color:#2d2d2d;
  margin-top: 8px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.search-tiezi-info > .index-tiezi-content {
  font-size: 12px;
  color: #474747;
  padding-right: 0.08rem;
  height: 0.4rem;
  overflow: hidden;
  text-overflow:ellipsis;
  -o-text-overflow:ellipsis;
  line-height: 0.2rem;
  word-break: break-all;
}
.who-tiezi-item3 {
  width: 100%;
  height: 0.4rem;
  margin-top: 0.2rem;
  border-top: 1px solid #F5F5F5;
}
.who-tiezi-item1 > .delete-tiezi {
  width: 0.52rem;
  height: 0.25rem;
  border: 1px solid #dfdfdf;
  border-radius: 0.04rem;
  position: absolute;
  right: 0.13rem;
  top: 0.13rem;
  text-align: center;
  color: #a9a9a9;
  font-size: 12px;
  line-height: 0.25rem;
}
.who-tiezi-item3 > .tiezi-read-box,
.who-tiezi-item3 > .tiezi-zanfh-box {
  width: 50%;
  height: 0.4rem;
  font-size: 14px;
  color: #9c9c9c;
  padding: 0.12rem 0.18rem;
}
.who-tiezi-item3 > .tiezi-read-box {
  float: left;
  position: relative;
}
.tiezi-read-box>.read-icon{
  display: inline-block;
  background: url(../images/eyes-read.png) center no-repeat;
  background-size: 0.2rem 0.2rem;
  width: 0.2rem;
  height: 0.2rem;
  position: absolute;
  left: 0.12rem;
  top: 0.1rem;
}
.tiezi-read-box>.tiezi-read-num{
  position: absolute;
  left: 0.34rem;
  top: 0.11rem;
}
.who-tiezi-item3 > .tiezi-zanfh-box {
  float: right;
}
.tiezi-zanfh-box > .tiezi-hui-box {
  height: 14px;
  float: left;
  position: relative;
}
.tiezi-hui-box > .tiezi-hui-icon {
  display: inline-block;
  width: 0.15rem;
  height: 0.15rem;
  background: url("../images/tiezi-huifu@3x.png") no-repeat;
  background-size: 0.15rem 0.15rem;
  position: absolute;
  left: -0.18rem;
  top: 0.03rem;
}
.tiezi-zanfh-box > .tiezi-zan-box {
  float: right;
  position: relative;
}
.tiezi-zan-box > .tiezi-zan-icon {
  display: inline-block;
  width: 0.15rem;
  height: 0.17rem;
  background: url("../images/tiezi-zan@3x.png") no-repeat center;
  background-size: 0.15rem 0.17rem;
  position: absolute;
  left: -0.18rem;
  top: 0.01rem;
}
 .download-item {
  width: 100%;
  height: 1rem;
  padding-top: 0.1rem;
  border-bottom: 1px solid #ccc;
  overflow: hidden;
  background: #fff;
}
.download-item > .download-item-link {
  display: block;
  width: 100%;
  height: 1rem;
  padding: 0.1rem;

}
.download-item > .download-item-link:nth-child(1) {
  padding-top: 0;
}
.download-item-link > .download-item-pic {
  width: 0.8rem;
  height: 0.8rem;
  position: absolute;
}
.download-item-link > .download-r {
  width: 100%;
  height: 1rem;
  margin-left: 0.9rem;
  overflow: hidden;
}
.download-r > .download-title {
  color: #2d2d2d;
  text-align: left;
  font-size: 18px;
  font-weight: 600;
  height: 0.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 0.2rem;
  width: 100%;
}
.download-r > .download-content {
  width: 100%;
  height: 0.34rem;
  font-size: 14px;
  color: #3e3e3e;
  margin-top: 0.05rem;
  overflow: hidden;
  padding-bottom: 4px;
  /*padding-right: 0.12rem;*/
  padding-right: 0.8rem;
}
.download-r > .download-time-box {
  margin-top: 0.05rem;
  color: #9c9c9c;
  font-size: 12px;
  height: 0.14rem;
  overflow: hidden;
}
.search-content-layout .mint-tab-container{
  position: absolute;
  overflow: visible;
}
.mint-tab-container .mint-tab-container-item{
  height: 100%;
}
.search-content-layout .mint-tab-container,.mint-tab-container-wrap{
  height: 100%;
  width: 100%;
}

.block-search{
  width: 100%;
  height: 100%;
  background: #EDEEF0;
  padding-top: 0.5rem;
}
.block-search .cate-title{
  width: 100%;
  height: 0.4rem;
  line-height: 0.4rem;
  background: #EDEEF0;
  color: #9c9c9c;
  font-size: 16px;
  padding-left: 10px;
}
.search-news{
  padding-top: 0.5rem;
}
.search-condition-box{
  width: 100%;
  position: absolute;
  left: 0;
  top: 0.4rem;
  background: #ffffff;
  padding: 0.1rem;
}
.search-item-text{
  width: 100%;
  height: 0.4rem;
  font-size: 18px;
  border-bottom: 1px solid rgba(204, 204, 204, 0.29);
}




