
.data-content-layout {
  width: 100%;
  height: 100%;
}
.data-content-topbar {
  width: 100%;
  height: 0.4rem;
  background-color: #3F3F3F;
  padding: 0.1rem 0  0 0.1rem;
  text-align: center;
  position: fixed;
  overflow:scroll;
  top: 0;
  left: 0;
}
.data-content-topbar > .datadl-content-back,
.data-content-topbar > .data-content-more {
  display: block;
  width: 0.1rem;
  height: 0.2rem;
}
.data-content-topbar > .datadl-content-back {
  background: url("../images/data-content-arrow.png") no-repeat;
  float: left;
  margin-top: 0.05rem;
}
.data-content-topbar > .data-content-more {
  background: url("../images/dat-content-more.png") no-repeat;
  float: right;
  width: 0.25rem;
  height: 0.4rem;
  margin-right: 0.1rem;
  margin-top: 0.06rem;
}
.data-content-topbar > .data-content-title {
  color: #ffffff;
  font-size: 14px;
  display: inline-block;
  height: 0.2rem;
  line-height: 0.2rem;
}
.data-content-article {
  width: 100%;
  height: 100%;
  padding-top: 0.4rem;
}
.data-content-article > .article-title {
  width: 100%;
  height: 0.25rem;
  margin: 0.15rem 0;
  text-align: center;
  font-size: 18px;
  color: #2D2D2D;
}
.data-content-article > .article-content {
  padding: 0 0.17rem;
  font-size: 14px;
  color: #000000;
}
.datajifen-download {
  width: 100%;
  height: 1.7rem;
  margin-top: 0.4rem;
  padding: 0 0.2rem;
  margin-bottom: 0.3rem;
}
.datajifen-download > .datajifen-dialog {
  width: 100%;
  height: 1.9rem;
  background: #f4f4f4;
  padding: 0.16rem 0.2rem 0 0.2rem;
  text-align: center;

}
.datajifen-dialog > .datajifen-need-title {
  font-size: 18px;
  color: #3c3b3b;
  height: 0.25rem;
}
.datajifen-dialog > .datajifen-how-get {
  font-size: 12px;
  color: #20a0ff;
  margin-top: 0.06rem;
}
.datajifen-dialog > .datajifen-download-btn {
  display: block;
  text-align: center;
  width: 100%;
  height: 0.45rem;
  background: #20a0ff;
  border-radius: 0.04rem;
  color: #ffffff;
  font-size: 18px;
  line-height: 0.45rem;
  margin-top: 0.16rem;
}
.datajifen-dialog > .datajifen-watch {
  width: 100%;
  height: 0.16rem;
  margin-top: 0.08rem;
}
.datajifen-watch > .watch-l,
.datajifen-watch > .watch-r {
  width: 50%;
  height: 0.16rem;
  font-size: 14px;
  color: #4e4e4e;
}
.datajifen-watch > .watch-l {
  float: left;
  text-align: right;
  padding-right: 0.3rem;
  position: relative;
}
.watch-l > .eyes {
  position: absolute;
  right: 0.58rem;
  top: 0.06rem;
}
.datajifen-watch > .watch-r {
  float: right;
  text-align: left;
  padding-left: 0.3rem;
}



