.personal_download_layer{
  width: 100%;
  height: 100%;
}
.login-header-bar {
    width: 100%;
    height: 0.4rem;
    text-align: center;
    background: #ffffff;
    position: fixed;
    z-index: 1;
    border-bottom: 2px solid #EDEEF0;
    top: 0;
    left: 0;
}
.login-header-bar .login-back-btn {
    float: left;
    width: 0.12rem;
    height: 0.19rem;
    display: inline-block;
    background: url("../images/back_login.png") no-repeat;
    margin: 0.10rem 0.1rem;
}
.login-header-bar .login-title {
    display: inline-block;
    font-size: 16px;
    line-height: 0.4rem;
    font-weight: 600;
    color: #2d2d2d;
}
.login-header-bar .topbar-left {
    float: right;
    font-size: 16px;
    color: #010101;
    line-height: 0.4rem;
    margin-right: 0.14rem;
}
.download-content-box {
    width: 100%;
    height: 100%;
    background: #ffffff;
    padding-top: 40px;
}
.download-content-box>.my-line{
    width: 100%;
    height: 10px;
    background: #EDEEF0;
}
.download-content-box .download-item {
    width: 100%;
    height: 100px;
    padding-top: 10px;
    border-bottom: 1px solid #ccc;
}
.download-item > .download-item-link {
    display: block;
    width: 100%;
    height: 100px;
    padding: 10px;
    overflow: hidden;
}
.download-item > .download-item-link:nth-child(1) {
    padding-top: 0;
}
.download-item-link > .download-item-pic {
    width: 80px;
    height: 80px;
    position: absolute;
}
.download-item-link > .download-r {
    width: 90%;
    height: 100px;
    margin-left: 90px;
    overflow: hidden;
}
.download-r > .download-title {
  color: #2d2d2d;
  text-align: left;
  font-size: 18px;
  font-weight: 600;
  height: 0.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 0.2rem;
  width: 250px;
}
.download-r > .download-content {
  width: 100%;
  height: 37px;
  font-size: 14px;
  color: #3e3e3e;
  margin-top: 5px;
  overflow: hidden;
  padding-bottom: 4px;
  word-break: break-all;
  /*padding-right: 0.12rem;*/
  padding-right: 80px
}
.download-r > .download-time-box {
    margin-top: 5px;
    color: #9c9c9c;
    font-size: 12px;
}
.upload-file-text{
  height: 40px;
  line-height: 40px;
  float: right;
  padding-right: 10px;
  position: absolute;
  right: 0;
  top: 0;
}



