.pcen-layout {
  width: 100%;
  height: 100%;
  background: #ffffff;
}
.jifendetail-top-bar {
  width: 100%;
  height: 0.4rem;
  background: #20A0FF;
  color: #ffffff;
  position: fixed;
  overflow:scroll;
  top: 0;
  left: 0;
}
.jifendetail-top-bar > .jifen-back-btn {
  display: inline-block;
  width: 0.14rem;
  height: 0.2rem;
  background: url("../images/jifen-back-icon.png") no-repeat;
  margin: 0.12rem 0.12rem;
}
.jifendetail-top-bar > .jifen-title {
  text-align: center;
  display: inline-block;
  height: 0.22rem;
  width: 2rem;
  line-height: 0.22rem;
  font-size: 16px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -0.11rem 0 0 -1rem;
}
.jifendetail-top-bar > .jifen-detail-text {
  float: right;
  font-size: 14px;
  margin-right: 0.26rem;
  margin-top: 0.11rem;
}
.jifen-detail-box {
  width: 100%;
  height: auto;
  padding: 0.4rem 0.2rem 0 0.2rem;
}
.jifen-detail-box .jifen-detail-item {
  width: 100%;
  height: 0.6rem;
  border-bottom: 1px dashed #cccccc;
}
.jifen-detail-item > .jifen-l {
  float: left;
  width: 70%;
  height: 100%;
}
.jifen-l > .get-jifen-m,
.jifen-l > .get-jifen-time {
  height: 0.2rem;
  font-size: 14px;
}
.jifen-l > .get-jifen-m {
  color: #000000;
  margin-top: 0.1rem;
}
.jifen-l > .get-jifen-time {
  color: #9a9a9a;
}
.jifen-detail-item > .jifen-r {
  float: right;
  width: 20%;
  height: 100%;
  line-height: 0.60rem;
  text-align: right;
  font-size: 16px;
  color: #7ed321;
}
.jifen-detail-item > .jifen-down {
  color: #d0021b;
}



