.write-tiezi-layout{
  max-width: 7.5rem;
  min-width: 3.2rem;
  height: 10000px;
  margin: 0 auto;
  background: #ffffff;
}
.write-tie-header {
  width: 100%;
  height: 0.4rem;
  text-align: center;
  background: #ffffff;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
}
/*.w-e-text,.w-e-text-container{
  width: 100%;
  -webkit-user-modify: read-write;
  height: 100%;
}*/
.write-tie-header .login-back-btn {
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat;
  margin: 0.1rem 0.1rem;
}
.write-tie-header .login-title {
  display: inline-block;
  font-size: 18px;
  line-height: 0.4rem;
  font-weight: 500;
  color: #2d2d2d;
}
.write-tie-header>.send-tiezi{
  float: right;
  height: 0.4rem;
  line-height: 0.4rem;
  padding: 0 0.1rem;
  color: #20a0ff;
  font-weight: 600;
  font-size: 18px;
}
.write-tie-header .topbar-left {
  float: right;
  font-size: 16px;
  color: #010101;
  line-height: 0.4rem;
  margin-right: 0.14rem;
}
.write-tie-box{
  width: 100%;
  height: 100%;
  padding: 0.4rem 0.12rem;

}
.write-tie-box >section{
  width: 100%;
  height: 0.4rem;
  margin-top: 0.2rem;
}
.write-tie-title>.tie-title{
  width: 100%;
  height: 0.4rem;
  border: 1px solid #cccccc;
  border-radius: 4px;
  font-size: 18px;
  padding-left: 0.1rem;
}
.write-tie-box>.write-tie-content{
  height: 100%;
}
.write-tie-box .w-e-toolbar{
  flex-wrap: wrap;
}
.write-tie-tags{
  height: auto;
}
#write-tie{
  width: 100%;
  height: 0.4rem;
  overflow: hidden;
  background: #ffffff;
  border: 1px solid #cccccc;
  border-radius: 4px;
  font-size: 16px;
  padding-left: 0.1rem;
}
option{
  word-break:break-all
}
.vue-html5-editor>.content img{
  width: 100%;
  height: 2.5rem;
}



