.feedback-history-header {
  width: 100%;
  height: 0.4rem;
  background: #f8f8f8;
  position: fixed;
  text-align: center;
  left: 0;
  top: 0;
  border-bottom: 1px solid #cccccc;
}
.feedback-history-header > .feedback-cancle {
  float: left;
  width: 0.12rem;
  height: 0.19rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat center;
  margin: 0.1rem;
}
.feedback-history-header > .feedback-title {
  width: 2rem;
  height: 0.22rem;
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -0.11rem 0 0 -1rem;
  color: #2d2d2d;
  font-size: 16px;
}
.history-main-layout{
  width: 100%;
  height: 100%;
  padding: 0.4rem 0.1rem 0.4rem 0.1rem;
  background: #ffffff;
}
.history-main-layout .history-item{
  width: 100%;
  margin-top: 0.2rem;
  height: auto;
  margin-bottom: 0.2rem;
}
.history-item .mine-history-info{
  width: 100%;
  float: left;
  background-color:#42B983;
  -moz-box-shadow: 4px 4px 5px #888888;
  box-shadow: 4px 4px 5px #888888;
  color: #ffffff;
  border-radius: 0 20px;
  margin: 0.1rem 0;
}
.mine-history-info>.mine-history-text{
  width: 100%;
  line-height: 0.26rem;
  font-size: 16px;
  padding: 0.1rem 0.1rem 0 0.1rem;
  word-break:break-all;
}
.mine-history-time{
  display: block;
  line-height: 0.2rem;
  padding: 0.1rem 0.1rem 0 0.1rem;
}



