.feedback-layout {
  width: 100%;
  height: 3rem;
  background: #ffffff;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}
.feedback-header {
  width: 100%;
  height: 0.4rem;
  background: #f8f8f8;
  position: relative;
  text-align: center;
}
.feedback-header > .feedback-cancle {
  float: left;
  height: 0.4rem;
  line-height: 0.4rem;
  color: #2d2d2d;
  font-size: 16px;
  margin-left: 0.1rem;
  font-weight: normal;
}
.feedback-header > .feedback-title {
  width: 2rem;
  height: 0.22rem;
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -0.11rem 0 0 -1rem;
  color: #2d2d2d;
  font-size: 16px;
}
.feedback-header > .feedback-send {
  float: right;
  border-radius: 0.04rem;
  width: 0.46rem;
  height: 0.24rem;
  margin-top: 0.08rem;
  color: #9c9c9c;
  text-align: center;
  margin-right: 0.1rem;
  line-height: 0.24rem;
  border:1px solid #9c9c9c;
  font-size: 14px;
  background: #ffffff;
}
.feedback-content-box {
  width: 100%;
  height: 4rem;
}
.feedback-content-box > .feedback-content {
  width: 100%;
  height: 100%;
  color: #2D2D2D;
  font-size: 16px;
  padding: 0.1rem;
  background: #ffffff;
}



