.avatar-layout{
  max-width: 7.5rem;
  min-width: 3.3rem;
  height: 100%;
  margin: 0 auto;
  background: #ffffff;
}

.avatar-header{
  height: 0.4rem;
  width: 100%;
  border-bottom: 1px solid #ededed;
  text-align: center;
}
.avatar-header>.avatar-back-btn{
  width: 0.12rem;
  height: 0.4rem;
  display: inline-block;
  background: url("../images/back_login.png") no-repeat center;
  margin-left: 10px;
  float: left;
}
.avatar-header>.avatar-title{
  height: 0.4rem;
  line-height: 0.4rem;
  font-size:16px;
  color:#2d2d2d;
}
.avatar-main{
  width: 100%;
  height: 100%;
  padding: 0.14rem 0.1rem;
}
.avatar-main>.avatar-img{
  text-align: center;
}
.avatar-i{
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  border: 1px solid #cccccc;
}
.avatar-choice{
  width: 100%;
  height: 2rem;
  padding: 10px;
  margin-top: 0.4rem;
}
.avatar-choice>.choice-xiangce{
  width: 100%;
  height: 0.4rem;
  background: #20A0FF;
  font-size: 18px;
  color: #ffffff;
  border: none;
}



