body .content {
  background: #fff;
  width: 100%;
  padding: 15px 0;
  overflow: hidden
}

body .content .tag-wrap {
  margin-bottom: 20px;
  overflow: hidden;
  /*padding: 0 15px*/
}

body .content .tag-wrap .tag {
  position: relative;
  float: left;
  background: #e1efff;
  height: 36px;
  line-height: 36px;
  padding: 0 20px;
  margin-right: 10px;
  margin-top: 4px;
}

body .content .tag-wrap .tag .txt {
  font-size: 16px;
  color: #2d2d2d
}

body .content .tag-wrap .tag .delete {
  position: absolute;
  top: 0;
  right: 0;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  background: #8ec1f8;
  color: #fff;
  font-size: 16px
}

body .content .tag-wrap .add-tag {
  float: left;
  height: 36px;
  border: 1px dashed #bbb;
  padding: 0 20px;
  box-sizing: border-box;
  margin-top: 4px;
}

body .content .tag-wrap .add-tag:hover {
  border: 1px dashed #2b8cfc
}

body .content .tag-wrap .add-tag span {
  float: left;
  line-height: 34px;
  font-size: 16px;
  color: #bbb
}

body .content .tag-wrap .add-tag span:first-child {
  font-size: 30px;
  margin-right: 4px;
  height: 34px;
  line-height: 30px;
}

body .content .article-wrap {
  width: 100%
}

body .content .article-wrap .title {
  overflow: hidden;
  /*padding: 0 15px*/
}

body .content .article-wrap .title input {
  width: 100%;
  height: 38px;
  line-height: 38px;
  border: 0;
  font-size: 20px;
  font-weight: 700;
  color: #2d2d2d;
  outline: 0;
  border-bottom: 1px solid #bbb
}

body .content .article-wrap .cont {
  width: 100%;
  padding: 10px 0;
  overflow: hidden
}

body .content .article-wrap .cont .editor {
  outline: 0;
  font-size: 16px;
  color: #2d2d2d;
  line-height: 22px;
  height: 200px;
  overflow-y: scroll;
  /*padding: 0 15px*/
}

body .content .article-wrap .cont .editor:empty:before {
  content: attr(placeholder);
  display: block;
  color: #aaa
}

body .content .article-wrap .toolbar {
  padding: 10px 0
}

body .content .article-wrap .toolbar .select-file {
  position: absolute;
  width: 60px;
  height: 60px;
  margin: 0;
  border: 1px solid #979797
}

body .content .article-wrap .toolbar .select-file:after, body .content .article-wrap .toolbar .select-file:before {
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: #979797
}

body .content .article-wrap .toolbar .select-file:before {
  width: 1px;
  height: 36px
}

body .content .article-wrap .toolbar .select-file:after {
  width: 36px;
  height: 1px
}

body .content .article-wrap .toolbar .select-file input {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-tap-highlight-color: transparent
}



