.jubao-layout{
  max-width: 7.5rem;
  min-width: 3.3rem;
  height: 100%;
  background: #EDEEF0;
  margin: 0 auto;
}
.jubao-content-topbar {
  width: 100%;
  height: 0.4rem;
  background-color: #3F3F3F;
  padding: 0.1rem 0  0 0.1rem;
  text-align: center;
  position: fixed;
  z-index: 1;
  overflow:scroll;
  top: 0;
  left: 0;
}
.jubao-content-topbar > .jubao-content-back,
.jubao-content-topbar > .jubao-content-more {
  display: block;
  width: 0.6rem;
  height: 0.4rem;
}

.jubao-content-topbar > .jubao-content-back {
  background: url("../images/data-content-arrow.png") no-repeat;
  float: left;
  margin-top: 0.05rem;
  position: relative;
}
.jubao-content-topbar > .jubao-content-back::after {
  color: #ffffff;
  font-size: 14px;
  content: '返回';
  position: absolute;
  right: 0.15rem;
  top: -0.04rem;
}
.jubao-content-topbar > .jubao-content-more {
  background: url("../images/dat-content-more@3x.png") no-repeat;
  background-size: 0.25rem;
  float: right;
  width: 0.25rem;
  height: 0.4rem;
  margin-right: 0.1rem;
  margin-top: 0.06rem;
}
.jubao-title{
  position: absolute;
  left: 50%;
  top: 25%;
  font-size:18px;
  color:#ffffff;
}
.jubao-main{
  width: 100%;
  height: 100%;
  padding-top: 0.4rem;
}
.jubao-main>.jubao-line{
  width: 100%;
  height: 0.1rem;
  background:#f6f6f6
}
.jubao-main>.jubao-content-box{
  width: 100%;
  height: 2.26rem;
  background: #ffffff;
  padding: 16px 15px 6px 12px;
}
.jubao-content-box>.who-title{
  width: 100%;
  height: 0.22rem;

}
.jubao-content-box>.who-title span{
  font-size: 16px;
  color: #3e3e3e;
}
.jubao-content-box>.who-title span.who-name{
  color: #20a0ff;
  display: inline-block;
}
.jubao-content-box>.who-content-box{
  height: 0.34rem;
  background: #f6f6f6;
  line-height: 0.34rem;
  padding:0 6px;
  margin-top: 6px;
  overflow: hidden;
}
.jubao-choice{
  width: 100%;
  height: 1.6rem;
}
.jubao-choice>.jubao-left{
  float: left;
  width: 50%;
}
.jubao-choice>.jubao-right{
  float: right;
  width: 50%;
}
.another-choice{
  width: 100%;
  height: 0.4rem;
}
.another-choice>.another-content{
  width: 100%;
  height: 0.4rem;
  border: 1px solid #cccccc;
  border-radius: 4px;
  font-size: 16px;
}
.jubao-btn{
  display: block;
  width: 100%;
  height: 0.44rem;
  background:#20a0ff;
  border-radius:4px;
  color: #ffffff;
  font-size: 20px;
  text-align: center;
  line-height: 0.44rem;
  margin-top: 0.15rem;
}
.jubao-ucontent{
  width: 20px;
  display: inline-block;
}



